import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { DanmakuMessage } from '../../../app/danmaku.def';

@Component({
  selector: 'yt-live-chat-text-message-renderer',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class MessageComponent implements OnInit {

  @Input("item") item:DanmakuMessage;

  constructor() { }

  ngOnInit() {
  }

  getType(){
    if(this.item.is_admin){
      return 'moderator';
    }
    else if(this.item.guard>0){
      return 'member';
    }
    return null;
  }

  getIcon(){
    if(this.item.guard==3){
      return "data:image/png;base64,";
    }
    else if(this.item.guard==2){
      return "data:image/png;base64,";
    }
    else if(this.item.guard==1){
      return "data:image/png;base64,";
    }
    else{
      return "";
    }
  }
}
